* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font-family: 'Poppins', sans-serif;
    background-color: #f5f5f5;
}
/* Navbar */
.navbar {
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 50px;
    position: fixed;
    width: 100%;
    height: 70px;
    top: 0;
    left: 0;
    z-index: 1000;
}
.navbar .left-section {
    display: flex;
    align-items: center;
    gap: 20px;
}
.navbar .logo {
    font-size: 24px;
    font-weight: bold;
    color: white;
}
.ai-name {
    font-size: 24px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    background: linear-gradient(135deg, #ff8c00, #ff4500);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
/* Search Bar */
.search-bar {
    display: flex;
    align-items: center;
    background: white;
    padding: 5px;
    border-radius: 20px;
}
.search-bar input {
    border: none;
    outline: none;
    padding: 8px 12px;
    border-radius: 20px;
    font-size: 16px;
    width: 200px;
}
.search-bar button {
    background: #ff8c00;
    border: none;
    color: white;
    padding: 8px 12px;
    border-radius: 20px;
    cursor: pointer;
    font-size: 16px;
    margin-left: 5px;
}
.search-bar button:hover {
    background: #ff4500;
}
/* Navbar Links */
.navbar .nav-links {
    display: flex;
    gap: 20px;
}
.navbar .nav-links a {
    color: white;
    text-decoration: none;
    font-size: 18px;
    transition: 0.3s;
}
.navbar .nav-links a:hover {
    color: #ff8c00;
}
/* Content - Push Down to Avoid Overlap */
.content {
    margin-top: 70px; /* Push below fixed navbar */
}
/* Full-Screen Carousel */ 
 .carousel {
    width: 100%;
    height: 100vh;
    position: relative;
}
.carousel img {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 1s ease-in-out;
}
.carousel img.active {
    opacity: 1;
}
.carousel-text {
    position: absolute;
    bottom: 15%;
    left: 50%;
    transform: translateX(-50%);
    color: white;
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.7);
}


.carousel {
position: relative;
width: 100%;
max-width: 1550px; /* Adjust as needed */
margin: auto;
}

.carousel img {
width: 100%;
display: block;
}

.overlay {
position: absolute;
top: 70%;
left: 50%;
transform: translate(-50%, -50%);
text-align: center;
color: white;
background: rgba(0, 0, 0, 0.6); /* Dark overlay */
padding: 20px;
border-radius: 10px;
width: 80%;
}

h2 {
font-size: 24px;
margin-bottom: 10px;
}

p {
font-size: 16px;
margin-bottom: 15px;
}

.btn {
background: orange;
color: black;
padding: 10px 15px;
border: none;
cursor: pointer;
font-size: 16px;
margin: 5px;
border-radius: 5px;
}

.btn.outline {
background: transparent;
border: 2px solid orange;
color: orange;
}



/* Navigation Buttons */
.prev, .next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 15px;
    cursor: pointer;
    font-size: 24px;
    border-radius: 50%;
}
.prev { left: 20px; }
.next { right: 20px; }
footer {
background-color: #2c3e50;
color: white;
padding: 20px;
text-align: center;
}

footer p {
margin: 0;
}

.footer-links {
margin-top: 10px;
}

.footer-links a {
color: white;
margin-left: 10px;
font-size: 14px;
text-decoration: none;
}

.footer-links a:hover {
text-decoration: underline;
}



/* How It Works Section */
.how-it-works {
    text-align: center;
    padding: 80px 20px;
    background-color: #1a1a1a;
    color: #fff;
}

.how-it-works h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
    color: #ff9800;
}

/* Steps Container */
.steps-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

/* Individual Step */
.step {
    background: #222;
    padding: 20px;
    width: 250px;
    border-radius: 10px;
    text-align: center;
    transition: 0.3s;
}

.step:hover {
    transform: scale(1.05);
    background-color: #333;
}

/* Icons */
.icon {
    font-size: 2.5rem;
    margin-bottom: 10px;
}

/* Headings */
.step h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #ff9800;
}

/* Descriptions */
.step p {
    font-size: 1rem;
    opacity: 0.8;
}


