/* General Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body {
    background-color: #0d0d0d;
    color: #ffffff;
    text-align: center;
    padding: 0;
}

/* Header */
header {
    background: #1a1a1a;
    padding: 60px 20px;
    text-align: center;
}

header h1 {
    font-size: 40px;
    font-weight: bold;
    color: #ff9800;
}

header p {
    font-size: 18px;
    color: #bdbdbd;
    margin-top: 10px;
}

/* About Section */
.about {
    max-width: 900px;
    margin: 50px auto;
    padding: 20px;
    text-align: left;
}

.about .content {
    background: #1e1e1e;
    padding: 25px;
    margin: 20px 0;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(255, 152, 0, 0.2);
}

.about h2 {
    color: #ff9800;
    margin-bottom: 10px;
}

.about p {
    font-size: 16px;
    color: #d9d9d9;
    line-height: 1.6;
}

.about ul {
    list-style: none;
    padding: 0;
}

.about ul li {
    background: rgba(255, 152, 0, 0.2);
    padding: 12px;
    margin: 6px 0;
    border-radius: 5px;
    font-size: 16px;
}


.profile-menu {
    position: relative;
    display: inline-block;
    cursor: pointer;
}

.profile-menu img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.dropdown-menu {
    display: none;
    position: absolute;
    background-color: white;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
    right: 0;
    min-width: 150px;
}

.dropdown-menu a {
    display: block;
    padding: 10px;
    text-decoration: none;
    color: black;
}

.dropdown-menu a:hover {
    background-color: #f1f1f1;
}
